import { useEffect, useRef } from "react";
import gsap from "gsap";

export default function Work() {
  const headingRef = useRef<HTMLDivElement>(null);
  const paragraphRef = useRef<HTMLDivElement>(null);
  
  useEffect(() => {
    if (!headingRef.current || !paragraphRef.current) return;
    if (window.matchMedia("(prefers-reduced-motion: reduce)").matches) return;

    const ctx = gsap.context(() => {
      gsap.to(headingRef.current, {
        filter: "blur(20px)",
        x: () => -window.innerWidth * 0.4,
        opacity: 0.5,
        ease: "power2.out",
        scrollTrigger: {
          trigger: headingRef.current,
          start: "top 50%",
          end: "bottom top",
          scrub: true,
        },
      });

      gsap.to(paragraphRef.current, {
        filter: "blur(20px)",
        x: () => window.innerWidth * 0.4,
        opacity: 0.5,
        ease: "power2.out",
        scrollTrigger: {
          trigger: paragraphRef.current,
          start: "top 50%",
          end: "bottom top",
          scrub: true,
        },
      });
    });

    return () => ctx.revert();
  }, []);

  return (
    <section>
      <div className="grid grid-cols-1 md:grid-cols-2">
        <div>
          <h2>Works.</h2>
        </div>
        <div>
          <p>
            A showcase of my work across diverse fields, including UI/UX design,
            web development, no-code solutions, and graphic design. I
            continuously learn new skills and tools, staying adaptable and open
            to new possibilities.
          </p>
        </div>
      </div>
    </section>
  );
}
